/* src/pages/Login.css */
.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #111;
    color: #f1c40f;
    text-align: center;
}

.login-container h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
}

.login-container form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-container input {
    background-color: #333;
    color: #f1c40f;
    border: 1px solid #f1c40f;
    padding: 12px;
    border-radius: 5px;
    width: 80%;
    margin: 10px 0;
}

.login-container input:focus {
    outline: none;
    border-color: #d4af37;
}

.cta-button {
    background-color: #f1c40f;
    color: #111;
    border: none;
    padding: 12px 25px;
    font-size: 1rem;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.cta-button:hover {
    background-color: #e0b90f;
    transform: scale(1.05);
}